.home {

    .homeContent {
      background: linear-gradient(-70deg, $color-1 50%, transparent 90%);
      min-height: $minHeight;
      width: 100%;
      min-height: 600px;
      border-radius: $borderRadius;
      position: relative;
      overflow: hidden;
  
      &:before {
        content: '';
        position: absolute;
        height: 100%;
        width: inherit;
        background: url('../../media/mamadou.jpg') no-repeat -60% 50% /80% 100% !important;
        z-index: -1;
        filter: brightness(60%);
      }

      h1 {
        position: absolute;
        left: 37%;
        top: 7%;
        font-size: 4rem;
        font-family: $font-3;
        color: $color-5;
        text-transform: uppercase;
        text-shadow: 3px 6px 2px $black;
      }
  
      .content {
        position: absolute;
        top: 45%;
        left: 10%;
        font-family: $font-1;
        display: inline;
        .desc {
          font-size: 2rem;
          width: 75%;
          padding: 4rem 0 5rem;
          margin-right: .5rem;
          color: $color-3;
          text-shadow: 1px 4px 1px $black;
        }
        .pdf {
          a {
            font-size: 1.2rem;
            background: $color-3;
            padding: 1rem 2rem;
            border-radius: $borderRadius;
            box-shadow: 0 5px 1px rgb(15, 15, 15);
            //transition: 0.2s ease;
  
            &:hover {
              background: $color-4;
            }
          }
          transform: scaleY(0);
        }
        .profil {
          position: absolute;
          color: $black;
          left: 40%;
          top:70%;
          //text-align: justify;
          margin-right: 1.5rem;
          background-color: lavender;
          padding: .5rem;
          border: solid lightblue 3px;
          border-radius: $borderRadius;
          //box-shadow: 0 5px 1px rgb(15, 15, 15);
          box-shadow: 0 0 0 1px $color-5, 0 0 0 2px $color-4, 0 0 0 3px $color-3, 0 0 0 4px $color-2;
          transform: scaleX(0);
        }
        //ANIMATION
        @mixin networkAnim($timeout) {
          animation: pop 1s forwards;
          animation-delay: $timeout;
        }
        @keyframes pop {
          to {
            transform: scaleX(1);
          }
        }
        .profil {
          @include networkAnim(.4s);
        }
        .pdf {
          @include networkAnim(.5s);
        }
      }
    }
  }
  @media screen and (max-width: 1120px){
    .homeContent {
      margin: 0 !important;
      .desc {
        font-size: 1.2rem !important;
      }
      .profil {
        margin-left: 2rem;
        top:70%;
        font-size: .8rem !important;
      }
    }
    
  }
  @media screen and (max-width:610px){
    .homeContent {
      margin-right: .2rem !important;
      margin-left: .2rem !important;
      &:after {
      background: url('../../media/mamadou.jpg') no-repeat 60% 50%/cover !important;
      }
      h1 {
        font-size: 2rem !important;
      }
      .desc {
        font-size: 1.2rem !important;
      }
      .profil {
        margin-left: 2rem;
        top:70%;
        font-size: .7rem !important;
      }
    }
  }

  @media screen and (max-width: 520px){
    .homeContent {
      .desc {
        font-size: .8rem !important;
      }
      .profil {
        margin-left: 2rem;
        top:60% !important;
        font-size: .55rem !important;
      }
      .content{
        .pdf {
          a{
            font-size: .9rem !important;
            padding: .5rem .5rem !important;
          }
          
        }
      }
    }
  }
