.sidebar {
    background: $color-1;;
    font-family: $font-1;
    height: 100%;
    min-height: $minHeight;
    width: 280px;;
    border-radius: $borderRadius;
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 280px 1fr 165px;
    grid-template-areas: 
        "1"
        "2"
        "3";
    .id {
        grid-area: 1;
        text-align: center;
        user-select: none;
        position: relative;

        .idContent {
            position: relative;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;
            margin-top: 1.5rem;
            
            img {
                border-radius: 50%;
                box-shadow: 0 0 0 4px $color-5, 0 0 0 8px $color-4, 0 0 0 12px $color-3, 0 0 0 16px $color-2;
                width: 70%;
            }

            h3 {
                font-size: 1.3rem;
                text-transform: uppercase;
                letter-spacing: .2rem;
                margin-top: 2rem;
                color: $color-5;
            }
        }
    }
    .navigation {
        grid-area: 2;
        padding: 2rem 1rem;

        ul {
            height: 100%;
            display: grid;
            margin-top: 4rem;

            li {
                padding: 1rem 0 1rem .4rem;
                font-size: 1.3rem;
                position: relative;

                &:hover i {
                    transform: translate(3px, 3px);
                }
                a {
                    color: $color-5;
                }
                .navActive {
                    color: $color-4;
                }

                i {
                    position: absolute;
                    font-size: 0.95rem;
                    transform: translateY(3px);
                    transition: .2s ease;
                }
                span {
                    margin-left: 2.7rem;
                }
            }
        }
    }
    .socialNetwork {
        grid-area: 3;
        margin-top: 3rem;
        ul {
            display: flex;
            width: 264px;
            padding: 0 14px;
            justify-content: space-between;

            li {
                font-size: 1.5rem;
                background: $color-5;
                color: $color-1;
                border-radius: 50%;
                padding: .5rem 0.8rem;
                transition: .3s;
                cursor: pointer;

                a:visited {
                    color: $color-1;
                }
                &:nth-child(1) {
                    padding: .5rem .85rem;
                }
                &:nth-child(2) {
                    padding: .5rem .75rem;
                }
                &:hover {
                    background: $color-4;
                }
            }
        }
    }
}

@media screen and (max-width: 1120px){
    .sidebar {
      margin: 0 auto;
      display: flex;
      min-height: 50px;
      margin-bottom: .2rem;
      width: 100%;
      .id, .socialNetwork {
        display: none;
      }
      .navigation {
        width: 100%;
        padding: .1rem 1rem;
  
        ul {
          margin: 0 auto;
          display: flex;
          justify-content: space-between;
  
          span {
            margin-left: 1.5rem !important;
          }
        }
      }
    }
  }
  @media screen and (max-width:620px){
    .sidebar {
      margin-right: .2rem !important;
      margin-left: .2rem !important;
      ul {
        justify-content: space-around !important;
  
        li {
          padding: 1rem 0 !important;
        }
        i {
          font-size: 1.4rem !important;
          transform: translateY(-3px) !important;
        }
        span {
          display: none;
        }
      }
    }
  }
