
.knowledges {
  .knowledgesContent {
    font-size: .9rem !important;
    min-height: $minHeight;
    width: 100%;
    position: relative;
    overflow: hidden;
    display: grid;
    grid-template-columns: 40% 1fr 1fr;
    grid-template-rows: 80% 20%;
    grid-template-areas: 
      "E E L"
      "O O H";

    .languagesFrameworks, .experience, .otherSkills, .hobbies {
      background: $color-1;
      border-radius: $borderRadius;
      padding: .5rem;
    }
    h3 {
      font-size: 1.4rem;
      margin-bottom: 1rem;
      font-family: $font-2;
      color: $color-5;
      text-transform: uppercase;
    }

    .languagesFrameworks {
      grid-area: L;
      margin-bottom: .5rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 57% 43%;
      grid-template-areas: 
        "L"
        "F";

      .frameworkDisplay {
        grid-area: F;
        //margin-top: .5rem;
      }
      h3 {
        margin-bottom: 0.3rem;
      }

      .years {
        font-size: .65rem;
        display: flex;
        //justify-content: space-between;
        color: $white;

        span {
          width: 36%;

          &:nth-child(2){
            text-align: center; 
            width: 20%;
          }
          &:nth-child(3){
            text-align: right;
            width: 30%;
          }
          &:nth-child(4){
            text-align: right;
            width: 30%;
          }
        
          &:nth-child(2):before,
          &:nth-child(3):before, &:nth-child(4):before {
            content:'';
            background: rgba(180, 179, 179, 0.507);
            position: absolute;
            width: 2px;
            transform: translate(9px, 19px);
          }
        }
      }
      #years-languages {
        span {
          &:nth-child(2):before,
          &:nth-child(3):before, &:nth-child(4):before {
            height: 300px;
          }
        }
      }
      #years-frameworks {
        span {
          &:nth-child(2):before,
          &:nth-child(3):before, &:nth-child(4):before {
            height: 230px;
          }
        }
      }
      .languagesList {
        padding-top: .5rem;

        li {
          color: $color-4;
        }
        .progressBar {
          height: .4rem;
          background: transparent;
          border-radius: 50px;
          margin-top: 0.2rem;
          position: relative;

          &:after {
            content: '';
            position: absolute;
            background: $color-2;
            border-radius: 50px;
            height: .4rem;
            width: 0;
            animation: line 2s ease-out forwards;

            @keyframes line {
              to {
                width: 100%;
              }
            }
          }
        }
      }
    }
    .experience {
      grid-area: E;
      margin: 0 .5rem .5rem .5rem;
      //display: grid;
      grid-template-columns: 1fr;
      //grid-template-rows: 5% 31% 40% 24%;
      grid-template-areas:
        "0"
        "1"
        "2"
        "3"
        "4";

      .expContent {
        margin-top: .5rem;
        padding-left: .5rem;

        .exp-head{
          display: flex;
          grid-template-columns: 1fr;
          grid-template-rows: 70% 30% ;
          grid-template-areas:
          "A"
          "B";
          .exp-left{
            padding-top: .5rem;
            display: flex;
            .extern-link {
              color: $color-3;
              &:hover {
                color: $color-5;
                font-style: italic;
              }
            }
            .period {
              padding-left: 2rem;
            }
            grid-area: A;
          }
          .exp-right{
            padding-left: 5rem;
            grid-area: B;
            img{
              border-radius: $imgBorderRadius;
              width: 5rem;
              &:hover{
                transform: scale(1.01);
              }
            }
          }
        }
      }
      h3 {
        grid-area: 0;
      }
      h4 {
        font-size: 1.1rem;
        color: $color-5;
      }
      .ville, .period {
        color: $color-3;
        padding: 0.2rem 0 0.2rem .2rem;

        i {
          color: $color-2;
        }
      }
      .sujet p {
        padding-left: 1rem;
      }
      .mission {
        ul {
          margin-top: .1rem;
          padding-left: 1.5rem;
        }
        li {
          list-style: inherit;
        }
      }
      .exp-0, .exp-1, .exp-2, .exp-3 {
        padding: .2rem 0 .1rem .2rem;
        color: $white;
      }
      h6 {
        font-size: .8rem;
        color: $color-4;
        padding: 0.3rem 0 0.2rem .2rem;
      }
      ul {
        li {
          padding: 0 0 0.2rem .1rem;
        }
      }
      /*.exp-0 {
        grid-area: 2;
        color: $white;
      }
      .exp-1 {
        grid-area: 2;
        color: $white;
      }
      .exp-2 {
        grid-area: 3;
        color: $white;
      }*/
      .exp-3 {
        //grid-area: 4;
        color: $white;

        #decat-exp {
          //justify-content: space-between;
          display: flex;
          #second {
            grid-area: J;
            margin-left: 6rem;
          }
        }
      }
    }
    .otherSkills {
      grid-area: O;
      margin-right: .5rem;
      margin-left: .3rem;
      .list {
        display: flex;
        width: 100%;
        height: 100%;
        margin: 0 auto;
        justify-content: space-between;
        ul {
          height: 80%;
          display: grid;
          li {
            color: $white;
          }
          i {
            color: $color-2;
          }
        }
      }
    }
    .hobbies {
      grid-area: H;
      padding-left: 1.5rem;

      ul {
        height: 80%;
        display: grid;

        li {
          i {
            position: absolute;
            color: $color-2;
          }
          span {
            position: absolute;
            transform: translateX(2.2rem);
            color: $white;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 1120px){
  .knowledgesContent {
    font-size: .5rem !important;
    h3 {
      font-size: 1.2rem !important;
    }
    .languagesList {
      padding-top: .2rem !important;
    }
    p, ul {
      font-size: .8rem;
    }
    .otherSkills, .hobbies {
      font-size: .8rem;
    }
  }
}
@media screen and (max-width: 850px){
  .knowledgesContent {
    h4 {
      font-size: .9rem !important;
    }
    .ville, .period {
      font-size: .6rem !important;
    }
  }
}
@media screen and (max-width: 680px){
  .exp-right{
    padding-left: 7rem !important;
    img{
      width: 6rem !important;
    }
  }
}
@media screen and (max-width: 500px){
  .knowledgesContent {
    display: grid !important;
    grid-template-columns: 1fr !important;
    grid-template-rows: 1fr 1fr 180px 180px !important;
    
    .languagesFrameworks {
      grid-area: 2 !important;
      margin-right: .2rem !important;
      margin-left: .2rem !important;
    }
    .experience {
      grid-area: 1 !important;
      margin-right: .2rem !important;
      margin-left: .2rem !important;

      .exp-0,.exp-1, .exp-2, .exp-3 {
        border: solid 1px $color-3;
        border-radius: $borderRadius;
        margin: .2rem;
        
        h4 {
          padding: .3rem 0 0 .4rem;
        }
        .expContent{
          .mission {
            ul{
              li{
                font-size: .5rem !important;
              }
            }
          }
        }
        .exp-right{
          padding-left: 3rem !important;
          padding-right: .2rem !important;
          img{
            border-radius: $imgBorderRadius;
            width: 4rem !important;
          }
        }
      }
    }
    .otherSkills {
      grid-area: 3 !important;
      margin-right: .2rem !important;
      margin-left: .2rem !important;

      .list{
        //width: 100%;
        padding-bottom: .5rem;
        ul {
          border: solid 1px $color-3;
          border-radius: $borderRadius;
          padding: .5rem;
          margin: .3rem;
          li {
            font-size: .7rem;
          }
        }
      }
    }
    .hobbies {
      grid-area: 4 !important;
      margin-top: .5rem !important;
      margin-right: .2rem !important;
      margin-left: .2rem !important;
    }
  }
}
