.portfolio {
    .portfolioContent {
      width: 100%;
      display: grid;
      min-height: $minHeight;
      grid-template-columns: 1fr;
      grid-template-rows: 0 1fr;
      grid-template-areas: 
        // "1"
        "2";
    }
    .projects {
      grid-area: 2;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      .project {
        background: $color-1;
        border-radius: $borderRadius;
        margin: 0.5rem 0 0 0;
        text-align: center;
        width: 30%;
        max-height: 50%;
        padding: .3rem;
        box-shadow: $shadow;
        display: grid;
        grid-template-rows: 15% 15% 55% 15%;
        grid-template-areas: 
          "1"
          "2"
          "3"
          "4";
      }
      .icons {
        grid-area: 1;
        display: flex;
        justify-content: center;
        @include verticalCenter;
        i {
          font-size: 1.5rem;
          padding: 0 .35rem;
          color: $color-5;
          text-shadow: 0 0px 1px $color-1;
        }
      }
      h3.project-title {
        grid-area: 2;
        font-family: $font-2;
        font-size: 1.3rem;
        @include verticalCenter;
        justify-content: center;
        color: $color-3;
      }
      img {
        grid-area: 3;
        margin: 0 auto;
        width: 65%;
        height: 100%;
        box-shadow: 0 2px 6px #333;
        border-radius: 4px;
        cursor: pointer;
        @include verticalCenter;
        
        &:hover {
          transform: scale(1.03);
        }
      }
      .infos {
        grid-area: 4;
        color: $color-4;
        cursor: pointer;
        font-size: 1.5rem;
        @include verticalCenter;
        transition: .2s;
        &:hover {
          transform: scale(1.1);
        }
      }
      .showInfos {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: rgba(0, 0, 0, 0.507);
        animation: popup .3s ease forwards ;
        transform-origin: center;
  
        .infosContent {
          position: relative;
          top: 25%;
          width: 600px;
          margin: 0 auto;
          color: $white;
          background: $color-1;
          border-radius: $borderRadius;
          padding: 2rem;
          box-shadow: 0 10px 10px #333;
          animation: popup .6s ease forwards;
          transform: scale(0);
          transform-origin: center;
  
          @keyframes popup {
            to {
              transform: scale(1);
            } 
          }
          .extern-link {
            color: $color-3;
            &:hover {
              color: $color-5;
              font-style: italic;
            }
          }
        }
        .head {
          display: flex;
          justify-content: space-around;
          margin: 1rem 0;
          color: $color-3;
        }
        h2 {
          text-align: left;
          margin-bottom: 1rem;
        }
        .button {
          cursor: pointer;
          color: $color-1;
          background: $color-5;
          border-radius: $borderRadius;
          padding: 1rem;
          transition: .2s;
  
          &:hover {
            background: $color-4;
          }
        }
        .text {
          text-align: justify;
          padding-bottom: 1rem;
        }
        .return {
          margin: 1rem auto;
          width: 150px;
        }
      }
    }
  }
  @media (max-width: 650px) and (min-width: 600px){
    .portfolioContent {
      .project {
        width: 30% !important;
        h3 {
          font-size: 1rem;
          //position: relative;
          left: 5%;
        }
        i {
          font-size: 1rem;
        }
      }
      .showInfos {
        .infosContent {
          width: 70% !important;
          .sourceCode {
            font-size: .6rem;
          }
          h2 {
            font-size: .8rem;
          }
          .return {
            margin: 0 auto;
            padding: 0;
            font-size: 1rem;
          }
        }
      }
    }
  }
  @media (max-width: 599px){
    .portfolioContent {
      display: inline;
      .project {
        grid-template-rows: 30px 80px 30px 40px !important;
        width: 90% !important;
        h3 {
          font-size: 1.5rem;
          left: 5%;
        }
        img {
          display: none;
        }
        .head {
          font-size: .8rem;
        }
        .text{
          font-size: .5rem;
        }
        .return {
          font-size: .5rem;
          width: 20%;
        }
      }
      .showInfos {
        .infosContent {
          width: 80% !important;
          h2 {
            font-size: .8rem;
          }
          .sourceCode {
            font-size: .5rem;
          }
          .return {
            margin: 0 auto;
            padding: 0 !important;
            font-size: 1rem;
          }
        }
      }
    }
  }
